/** 
 *
 * @param {Object} obj 
 * @param {string?} id id
 * @param {string?} superior superior
 */
export function sortBySuperiorOrder(obj, id = 'id', superior = 'superior') {
  if (!obj || typeof obj !== 'object') {
    throw new Error('object');
  }
  const keys = Object.keys(obj);
  const resource = keys.map(k => obj[k]);// 
  const entities = {}; // superior,id()
  let resource2 = []; // 
  const byDepth = {}; // depth
  const arrDepth = []; // depth(desc)
  const compare = (v1, v2) => parseInt(v2, 10) - parseInt(v1, 10);


  // arrary(object)  key-valuekeysuperiorvalue
  resource.forEach((r) => {
    if (entities[r.superior]) {
      entities[r.superior].push(r);
    } else {
      entities[r.superior] = [r];
    }
  });

  // order,spreadresource2
  Object.keys(entities).forEach((k) => {
    entities[k].sort((o1, o2) => parseInt(o1.order, 10) - parseInt(o2.order, 10));
    resource2 = [...resource2, ...entities[k]];
  });

  /** byDepth,depthmap
   * {
   *   [depth]: map([id]: [{data}])
   * }
   */
  resource2.forEach((r) => {
    const { depth } = r;

    // 
    if (byDepth[depth]) {
      if (byDepth[depth].has(r[id])) {
        byDepth[depth].get(r[id]).push(r); // id
      } else {
        byDepth[depth].set(r[id], [r]); // 
      }
    } else {
      arrDepth.push(r.depth); // depth()
      byDepth[depth] = new Map([[r[id], [r]]]);// map[]
    }
  });

  // ,d(),o(Object)
  arrDepth.sort(compare);
  arrDepth.forEach((d) => { // depth
    if (d > 0) {
      byDepth[d].forEach((value, key) => { // mapmapkeyidvalue
        if (!byDepth[d - 1]) {
          throw Error('depth0');
        }
        // 
        if (!byDepth[d - 1].get(byDepth[d].get(key)[0][superior])[0].hasChildren) {
          byDepth[d - 1].get(byDepth[d].get(key)[0][superior])[0].hasChildren = true;
        }
        // valuepush
        value.forEach(v => byDepth[d - 1].get(byDepth[d].get(key)[0][superior]).push(v));
      });
    }
  });

  return byDepth['0'];// mapdepth === 0(id)value
  /**
   * 
   * const catalogList = []
   * result.forEach((r) => r.forEach(item => catalogList.push(item)))
  */
}

export default sortCatalog;



